function CreateOrder() {

	const [step, setStep] = useState<number>(1)

	const mySetStep = (step: number) => {
		setStep(step)
	}

	return (
		<>
			<RB.Card style={{border: 0, paddingTop: '5%'}}>
				<RB.Card.Body style={{backgroundColor: '#ffffff', borderRadius: '0.375rem'}}>
					{step === 1 && <CreateOrderStep1 setStep={mySetStep} step={2}/>}
					{step === 2 && <CreateOrderStep2 setStep={mySetStep} step={3}/>}
					{step === 3 && <CreateOrderStep3 setStep={mySetStep} step={4}/>}
					{step === 4 && <CreateOrderStep4 setStep={mySetStep} step={5}/>}
					{step === 5 && <CreateOrderStep5 setStep={mySetStep} step={6}/>}
				</RB.Card.Body>
				<RB.Card.Footer style={{border: 0, backgroundColor: '#ffffff'}}/>
			</RB.Card>
		</>
	)
}

